.options_list{
  margin: auto;
  height: 29px;
  padding-bottom: 5px;
  border: 0.5px solid $blue-dark;
  border-radius: 4px;
}

.option{
  height: 27px;
  width: 118px;
  padding: 7px 0 0 0;
  text-transform: capitalize;
  cursor: pointer;
	
	&:nth-child(1), &:nth-child(2) {
		border-right: 0.25px solid $blue-dark;
  }
  &:nth-child(1) {
    border-radius: 4px 0 0 4px;
    border-right: 0.5px solid $blue-dark;
  }
  &:nth-child(3) {
    border-radius: 0 4px 4px 0;
  }
}

.selected-option{
  background-color: $blue-dark;	
  color:$color-light;
}

.delete-btn{
  font-family: HelveticaNeue;
  font-size: 18px;
  color: $blue-dark;
  text-transform: capitalize;
  background-color: white;
  border: none;
  margin-left: 16px;
  cursor: pointer;
  position: relative;
  top: 96px;
  right: 380px;
  height: 22px;
}
.boxes_date {
